import React from 'react'
import './FurnitureFlow.css'
import flowLight from '../../assets/images/flow-light.png'

const FurnitureFlow = () => {
  return (
    <div>
        <img className='bg-center bg-no-repeat bg-cover h-full w-full' src={flowLight} alt="" />
    </div>
  )
}

export default FurnitureFlow